import React, { useState, useEffect } from "react";
import "./PartyTemplate.css";
import ViewFinalDetails from "../viewFinalDetails/ViewFinalDetails";
import jsPDF from "jspdf";

export default function PartyTemplate(props) {
    const selectStateArray = props.state;

    selectStateArray.sort((a, b) => a.name.localeCompare(b.name));

    const amountArr = Array(selectStateArray.length).fill(Number(0));
    const disableArr = Array(selectStateArray.length).fill(false);
    const disableEditBtnArray = Array(selectStateArray.length).fill(true);

    const [biharTestArr, setBiharTestArr] = useState(selectStateArray);

    const [indexValue, setIndexValue] = useState(Number(0));

    const [updateAmountArr, setupdateAmountArr] = useState(amountArr);

    const [disableInputArr, setdisableInputArr] = useState(disableArr);

    const [disableEditBtn, setdisableEditBtn] = useState(disableEditBtnArray);

    const amount = undefined;

    const [showDetails, setshowDetails] = useState(false);

    const [valueX, setValueX] = useState(0);

    const [valueY, setValueY] = useState(0);

    useEffect(() => {
        setBiharTestArr(selectStateArray);
    }, [selectStateArray]);

    const updateAmount = (e) => {
        const i = e.target.id;
        const rtgsAmount = e.target.value;

        const cloneAmountArr = [...updateAmountArr];
        cloneAmountArr[i] = Number(rtgsAmount);
        setupdateAmountArr(cloneAmountArr);
    };

    const savePartyAmount = (e) => {
        let index = e.target.value;

        const newStateList = [...biharTestArr];
        newStateList[index] = {
            ...newStateList[index],
            amount: updateAmountArr[index]
        };
        setBiharTestArr(newStateList);

        const newDisableInputArr = [...disableInputArr];
        newDisableInputArr[index] = !disableInputArr[index];
        setdisableInputArr(newDisableInputArr);

        const newDisableEditArr = [...disableEditBtn];
        newDisableEditArr[index] = !disableEditBtn[index];
        setdisableEditBtn(newDisableEditArr);
    };

    const enableInputSave = (e) => {
        let index = e.target.value;

        const newEnableInput = [...disableInputArr];
        newEnableInput[index] = ![disableInputArr[index]];
        setdisableInputArr(newEnableInput);
    };

    const generatePdf = (e) => {
        const index = e.target.value;
        const selectedParty = selectStateArray[index];
        var imageData =
            "data:image/jpeg;base64,";
        var doc = new jsPDF();
        doc.setFontSize(13);
        doc.addImage(imageData, "JPEG", 0, -20, 210, 320, null, null, -0.4);
        var baseBranch = "Mohania";
        var myAccNumber = "00070400000418";
        var myAccHolder = "AMIT PHARMA";
        var time = "11:00 a.m.";
        var myMobNo = "9431089076";
        var todayDate = new Date();
        var todayYear = todayDate.getFullYear();
        var todayMonth = todayDate.getMonth() + 1;

        doc.text(`${baseBranch}`, 160, 230, null, -90);
        doc.text(`${myAccNumber}`, 154.2, 230, null, -90);
        doc.text(`${myAccHolder}`, 149, 230, null, -90);
        doc.text(`${baseBranch}`, 182, 110, null, -90);
        doc.text(`${time}`, 186, 265, null, -90);
        doc.text(`/${todayMonth}/${todayYear}`, 193, 265, null, -90);

        var beneficiaryBank = selectedParty.bank;
        var beneficiaryBranch = selectedParty.branch;
        var beneficiaryIFSC = selectedParty.ifscCode;
        var beneficiaryAccNO = selectedParty.accNo;
        var beneficiaryName = selectedParty.name;

        doc.text(`${beneficiaryBank}`, 134, 220, null, -90);
        doc.text(`${beneficiaryBranch}`, 128.4, 220, null, -90);
        doc.text(`${beneficiaryIFSC}`, 122.7, 220, null, -90);
        doc.text(`${beneficiaryAccNO}`, 118, 220, null, -90);
        doc.text(`${beneficiaryAccNO}`, 93, 220, null, -90);
        doc.setFontSize(12);
        doc.text(`${beneficiaryName}`, 108, 200, null, -90);
        doc.setFontSize(10);
        doc.text(`${myMobNo}`, 24, 123, null, -90);

        if (biharTestArr[index].amount === 0) {
            doc.setFontSize(20);
            doc.text("_________", 116, 110, null, -70);
        } else {
            doc.setFontSize(20);
            var amount = biharTestArr[index].amount;
            doc.text(`${amount}.00`, 116, 110, null, -70);
        }

        var string = doc.output("datauristring");
        var embed = "<embed width='100%' height='100%' src='" + string + "'/>";
        var x = window.open();
        x.document.open();
        x.document.write(embed);
        x.document.close();
    };

    const showDetailsOfFirm = (e) => {
        const index = e.target.value;
        setValueX(e.pageX);
        setValueY(e.pageY);
        setshowDetails(!showDetails);
        setIndexValue(index);
    };

    return (
        <div className="mb-5">
            {biharTestArr.map((p, i) => (
                <div
                    key={i}
                    className="flex justify-between items-center each-row font-bold"
                >
                    <div className="flex">
                        <div className="lg:mx-5">{i + 1}.</div>
                        <div>
                            <button className="" onClick={showDetailsOfFirm} value={i}>
                                {p.name}
                            </button>
                        </div>
                    </div>

                    <div className="flex justify-around">
                        <div className=" text-black mx-3">
                            <input
                                className="input h-8 w-48"
                                type="number"
                                placeholder="Enter Amount"
                                value={amount?.[i]}
                                id={i}
                                onChange={updateAmount}
                                disabled={disableInputArr[i]}
                            />
                        </div>

                        <div>
                            <button
                                className={
                                    disableInputArr[i]
                                        ? "pointer-events-none btn-save-party-amt mx-1"
                                        : "btn-save-party-amt mx-1"
                                }
                                onClick={savePartyAmount}
                                value={i}
                                disabled={disableInputArr[i]}
                            >
                                Save Amount
                            </button>
                        </div>

                        <div>
                            <button
                                disabled={!disableInputArr[i]}
                                value={i}
                                className={
                                    !disableInputArr[i]
                                        ? "hide-edit-btn mx-3"
                                        : "show-edit-btn mx-3"
                                }
                                onClick={enableInputSave}
                            >
                                Edit
                            </button>
                        </div>

                        <div>
                            <button
                                value={i}
                                className="show-edit-btn mx-3"
                                onClick={generatePdf}
                            >
                                PDF
                            </button>
                        </div>
                    </div>
                </div>
            ))}

            {showDetails && (
                <ViewFinalDetails
                    data={selectStateArray}
                    index={indexValue}
                    valueX={valueX}
                    valueY={valueY}
                />
            )}
        </div>
    );
}
